.booking {
  margin-bottom: 0;
}

.booking__title {
  font-size: 21px;
  line-height: 26px;
}

.booking__text {
  width: 280px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 58px;
}

.booking__location {
  margin-top: 49px;
  width: 320px;
  height: 351px;

  background-image: url("../img/bg-map-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 0 0;
}

.booking__br {
  display: none;
}

@media (min-width: 768px) {
  .booking__title {
    font-size: 30px;
  }

  .booking__text {
    width: 440px;
    line-height: 24px;
  }

  .booking__location {
    width: 768px;
    height: 352px;

    background-image: url("../img/bg-map-tablet@1x.jpg");
  }

  .booking__br {
    display: initial;
  }
}

@media (min-width: 1200px) {
  .booking__text {
    margin-bottom: 67px;
  }

  .booking__location {
    width: 1200px;

    background-image: url("../img/bg-map-desktop@1x.jpg");
  }
}
